



























import Vue from "vue";
export default Vue.extend({
  name: "QOtp",
  props: {
    length: {
      type: Number,
      default: 4
    },
    disabled: {
      type: Boolean,
      default: false
    },
    type: {
      type: String,
      default: "text"
    },
    value: {
      type: Array
    },
    preventPaste: {
      type: Boolean,
      default: true
    },
    width: {
      type: String,
    },
    height: {
      type: String,
    },
    readonly: {
      type: Boolean
    },
    error: {
      type: Boolean,
      default: false
    },
    errorMessage: {
      type: String
    },
    customClass: {
      type: String,
      default: ""
    },
    inputMode: {
    type: String
  }
  },
  data() {
    return {
      numInputs: this.length,
      otp: Array(this.length).fill(""),
      valid: true,
      firstClick: false,
    };
  },
  created() {
    if (this.value) {
      this.otp = this.value;
    }

    const self = this;
        window.addEventListener("click", function (e) {
            let refOtp = <HTMLElement>self.$refs["otpContainerRef"];
            if (e.target && !refOtp?.contains(e.target as HTMLInputElement)) {
              if(self.firstClick) {
                self.$emit("blur", self.otp.join(''));
                self.validateInput();
              }
                self.firstClick = false;
            }
    });
  },
  mounted() {
    Object.defineProperty(this, "value", {
      get: () => this.otp,
      set: (value) => (this.otp = value),
    });
  },
  methods: {
    handleInput(index, event) {
      if (index < this.numInputs) {
        const currentValue = this.otp[index];
        const focus = () => {
          if (currentValue.length === 1) {
            (this.$refs[`input-${index + 1}`] as HTMLElement)?.focus();
          }
        };

        if (this.type === "number") {
          if (/^[0-9]$/.test(currentValue)) {
            focus();
          } else {
            this.otp[index] = "";
          }
        } else {
          focus();
        }
        this.firstClick = true;
        this.$emit("input", this.otp.join(''))
        this.onFinish();
      }
    },
    handleDelete(index) {
      if (index > 0 && !this.otp[index]) {
        (this.$refs[`input-${index - 1}`] as HTMLElement).focus();
      }
    },
    handlePaste(event) {
      if (this.preventPaste === true) {
        const paste = (event.clipboardData || window.Clipboard).getData("text");
        const pasteLength = paste.length;

        if (pasteLength > this.length) {
          this.otp = paste.substr(0, this.length).split("");
        } else {
          this.otp = paste.split("");
        }
        (this.$refs[`input-${this.otp.length - 1}`] as HTMLElement).focus();
      } else {
        event.preventDefault();
      }
    },
    onFinish() {
      if (this.numInputs === this.otp.length && this.otp.every(val => val !== "")) {
        this.$emit("finish", this.otp.join(''));
        this.validateInput();
      }
    }, 
    handleRightArrowKey(index) {
      if (index < this.numInputs - 1) {
        (this.$refs[`input-${index + 1}`] as HTMLElement)?.focus();
      }
    },
    handleLeftArrowKey(index) {
      if (index > 0) {
        (this.$refs[`input-${index - 1}`] as HTMLElement)?.focus();
      }
    },
    validateInput() {
      this.valid = this.otp.length === this.numInputs  && this.otp.every(val => val !== "");
      let element = document.getElementsByClassName('otp-input');
      if(this.valid === true) {
        Array.from(element).forEach((element) => {
            element.classList.remove('error-input');
        });
      }else {
        if(this.error === true) {
          Array.from(element).forEach((element) => {
            element.classList.add('error-input');
          });
        }
      }
    }
  },
});
